import { Injectable } from '@angular/core';
import { isString } from 'util';

import { TaxonomyRecordInterface } from "../interface/taxonomy-record.interface";
import { TaxonomyTypeInterface } from "../interface/taxonomy-type.interface";
import { TaxonomyParser } from "../parser/taxonomy.parser";

@Injectable()
export class TaxonomyService {
  private taxonomy: TaxonomyRecordInterface[] = [];

  constructor(parser: TaxonomyParser) {
    this.add(...parser.parse());
  }

  add(...r: TaxonomyRecordInterface[]) {
    this.taxonomy.push(...r);
  }

  getType(t: TaxonomyTypeInterface | string) {
    const sr = isString(t) ? t : (t as TaxonomyTypeInterface).id;

    return this.taxonomy.filter(t => t.type.id === sr);
  }

  getTypes(ts: (TaxonomyTypeInterface | string)[]) {
    const ids = ts.map(t => isString(t) ? t : (t as TaxonomyTypeInterface).id);

    return this.taxonomy.filter(t => ids.includes(t.type.id));
  }

  getAll() {
    return [...this.taxonomy];
  }
}
