import { Injectable } from '@angular/core';
import { combinations } from "../../../core/helper/combinations";

import { TaxonomyParserInterface } from '../../interface/taxonomy-parser.interface';
import { TaxonomyRecordInterface } from '../../interface/taxonomy-record.interface';

import TEACHERS from 'src/app/configuration/teacher/teachers';
import { TeacherType } from "../../type/teacher.type";

@Injectable()
export class TeacherParser implements TaxonomyParserInterface {
  get(): TaxonomyRecordInterface[] {
    const teacherType = new TeacherType();

    return TEACHERS.map(t => {
      return {
        type: teacherType,
        texts: [
          ...combinations([t.first_name, t.surname, t.last_name]),
          t.first_name,
          t.last_name,
          t.surname,
          t.description.replace(/<\w+\/?>/g, '')
        ].map(text => text.replace(/(\s){2,}/g, '$1')),
        reference: t
      }
    });
  }
}
