import { combinations } from "../../../core/helper/combinations";
import { PageTaxonomyInterface } from "../interface/page-taxonomy.interface";
import teachers from '../../teacher/teachers';

export default {
  url: '/employee',
  display: 'Працівники',
  keywords: [
    'працівник', 'професор', 'доцент',
    ...teachers
      .map(t => combinations([t.last_name, t.first_name, t.surname]))
      .reduce((a, b) => a.concat(b), [])
  ]
} as PageTaxonomyInterface;


