function make(arr, el) {
  let i;

  const len = arr.length;
  const res = [];

  for(i = len; i >= 0; i--) {
    res.push(
      ([]).concat(
        arr.slice(0, i),
        [el],
        arr.slice(i)
      )
    );
  }

  return res;
}

export function combinations(arr: string[]): string[] {
  let prev, curr, el, i;
  const len = arr.length;

  curr = [[arr[0]]];

  for(i = 1; i < len; i++) {
    el = arr[i];
    prev = curr;
    curr = [];

    prev.forEach(function(item) {
      curr = curr.concat(
        make(item, el)
      );
    });
  }

  return curr.map(c => c.join(' '));
}
