import { Injectable } from '@angular/core';

import { TaxonomyParserInterface } from '../../interface/taxonomy-parser.interface';
import { TaxonomyRecordInterface } from '../../interface/taxonomy-record.interface';

import PAGES from 'src/app/configuration/page/page';
import { PageType } from "../../type/page.type";

@Injectable()
export class PageParser implements TaxonomyParserInterface {
  get(): TaxonomyRecordInterface[] {
    const pageType = new PageType();

    return PAGES.map(t => {
      return {
        type: pageType,
        texts: t.keywords,
        reference: t
      }
    });
  }
}
