import { Injectable } from '@angular/core';

import { TaxonomyRecordInterface } from "../interface/taxonomy-record.interface";
import { PageParser } from "./list/page.parser";
import { TeacherParser } from "./list/teacher.parser";

@Injectable()
export class TaxonomyParser {
  constructor(private teacher: TeacherParser,
              private page: PageParser) {
  }

  parse(): TaxonomyRecordInterface[] {
    return [
      ...this.teacher.get(),
      ...this.page.get()
    ]
  }
}
